.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--backdrop-cl);
  backdrop-filter: blur(2px);
  transition-property: opacity, visibility;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1) rotate(0);
  width: 528px;
  height: auto;
  padding-top: 40px;
  padding-right: 40px;
  padding-bottom: 40px;
  padding-left: 40px;
  background-color: var(--background-white-cl);
  border-radius: 4px;
  box-shadow: var(--modal-shadow);
  transition-property: transform, opacity;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transform: translate(-50%, -50%) scale(0) rotate(90deg);
}

.close-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 8;
  right: 8;
  width: 30px;
  height: 30px;
  padding: 0;
  border-width: 1px;
  border-radius: 50%;
  border-color: var(--btn-border-cl);
  background-color: transparent;
  transition-property: fill;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.close-btn:hover,
.close-btn:focus {
  fill: var(--accent-cl);
}

.register-form-title {
  display: block;
  font-size: 20px;
  line-height: calc(23 / 20);
  color: var(--title-txt-cl);
  text-align: center;
  margin-bottom: 12px;
}

.register-form-group {
  margin-bottom: 20px;
}

.register-form-wrap {
  display: block;
  margin-bottom: 10px;
}

.register-form-wrap:last-child {
  margin-bottom: 0;
}

.register-form-label {
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  line-height: calc(14 / 12);
  letter-spacing: 0.01em;
  margin-bottom: 4px;
}

.register-form__thumb {
  position: relative;
}

.register-form__input,
.register-form__message {
  width: 100%;
  height: 40px;
  border-width: 1px;
  border-style: solid;
  border-color: var(--input-border);
  border-radius: 4px;
  outline: transparent;
  padding-right: 12px;
  padding-left: 40px;

  transition-property: border-color;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.register-form-icon {
  position: absolute;
  top: 50%;
  left: 12;
  transform: translateY(-50%);
  color: var(--icon-modal-cl);
  transition-property: color;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.register-form__input:hover,
.register-form__input:focus,
.register-form__message:hover,
.register-form__message:focus,
.register-form__input:hover + .register-form-icon,
.register-form__input:focus + .register-form-icon,
.register-form-icon:hover {
  color: var(--accent-cl);
  border-color: var(--accent-cl);
}

.register-form-use {
  fill: currentColor;
}

.register-form__message {
  height: 120px;
  padding-top: 12px;
  padding-right: 16px;
  padding-bottom: 12px;
  padding-left: 16px;
  resize: none;
}

.register-form__message::placeholder {
  font-size: 12px;
  line-height: calc(14 / 12);
  letter-spacing: 0.01em;
  color: var(--placeholder-txt-cl);
}

.register-agreement-label {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  cursor: pointer;
  margin-bottom: 30px;
}

.register-agreement-input {
  display: none;
}

.register-agreement-icon {
  color: var(--icon-modal-cl);
  transition-property: color;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.register-agreement-label:hover .register-agreement-icon {
  color: var(--accent-cl);
}

.register-agreement-checked {
  opacity: 0;
  transition-property: opacity;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.register-agreement-unchecked {
  fill: currentColor;
}

.register-agreement-input:checked
  + .register-agreement-icon
  .register-agreement-checked {
  opacity: 1;
}

.register-agreement-text {
  font-size: 14px;
  line-height: calc(24 / 14);
  letter-spacing: 0.03em;
}

.register-agreement-link {
  text-decoration: underline;
  text-underline-position: under;
  color: var(--accent-cl);
}

.register-form-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  padding-top: 10px;
  padding-right: 52px;
  padding-bottom: 10px;
  padding-left: 52px;
  box-shadow: var(--modal-btn-shadow);
}
