.header {
  font-weight: 500;
  font-size: 14px;
  line-height: calc(16 / 14);
  letter-spacing: 0.02em;
}

.header-item:not(:last-child) {
  margin-right: 50px;
}

.header-icon {
  margin-right: 10px;
  fill: currentColor;
}

.header-contacts__list {
  display: flex;
  margin-left: auto;
}

.header-line {
  border-bottom: 1px solid var(--header-border-cl);
}
